import styled from 'styled-components';
import {AutoField, BoolField} from "uniforms-antd";


export const LinceAutofield = styled(AutoField)`    
    padding: 2px 10px;
    box-sizing: border-box;
    margin: 0 0 0 5px;    
    width: 100%;
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    font-size: 14px;
    line-height: 1.3;
    background-color: #fff;
    background-image: none;
`;

export const LinceBoolfield = styled(BoolField)` 
    cursor:pointer;   
    padding: 2px 10px;
    box-sizing: border-box;
    margin: 0 0 0 5px;    
    width: 100%;
    border: 0px solid #d9d9d9;
    border-radius: 2px;
    font-size: 14px;
    line-height: 1.3;
    background-color: #fff;
    background-image: none;
`;

