/* eslint-disable react/prop-types */
import React, {Children} from 'react'
import {connectField, joinName} from 'uniforms'
import {AutoField, ListDelField} from 'uniforms-antd'
import {faArrowDown, faArrowUp} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {Button, Tooltip} from 'antd';
import {moveArrayElement} from "./moveArrayElement";
import {DeleteOutlined} from "@ant-design/icons";

const moveUp = (array, index) => {
    return moveArrayElement(array, index, -1);
}
const moveDown = (array, index) => {
    return moveArrayElement(array, index, 1);
}
const styles = {
    root: {
        display: 'flex',
        width: '100%',
        alignItems: 'flex-start',
    },
    fieldsColumn: {
        width: '100%',
        paddingTop: '5px'
    },
    toolboxColumn: {
        display: 'flex',
        /*flexDirection: 'column',*/
        paddingTop: "0px",
    },
}
const ListItem = ({dense, divider, disableGutters, removeIcon, parent, ...props}) => {
    const autoFieldProps = {
        ...props,
        labelCol: {span: 4},
        wrapperCol: {span: 4},
        //label:false,
        labelProps: {shrink: true, disableAnimation: true}
    };
    //default values for ordering buttons
    if (props.name && !props.moveDown && !props.moveUp) {
        const fieldIndex = +props.name.slice(1 + props.name.lastIndexOf('.'));// same than ListDelField
        props.moveUp = () => {
            let result = moveUp(parent.value, fieldIndex);
            parent.onChange([].concat(result))
        }
        props.moveDown = () => {
            let result = moveDown(parent.value, fieldIndex);
            parent.onChange([].concat(result))
        }
        props.moveUpDisabled = (fieldIndex === 0);
        props.moveDownDisabled = (fieldIndex === (parent.value.length - 1))
    }
    return <div style={styles.root}>
        <div style={styles.fieldsColumn}>
            {props.children ? (
                Children.map(props.children, child =>
                    React.cloneElement(child, {
                        name: joinName(props.name, child.props.name), label: null,
                    }),
                )
            ) : (<AutoField {...autoFieldProps} />)}
        </div>
        <div style={styles.toolboxColumn}>
            {props.allowOrdering &&
            <>
                <Tooltip title="up">
                    <Button type="primary" shape="circle"
                            disabled={props.moveUpDisabled} onClick={props.moveUp}
                    ><FontAwesomeIcon icon={faArrowUp}/></Button>
                </Tooltip>
                &#160;
                <Tooltip title="down">
                    <Button type="primary" shape="circle"
                            disabled={props.moveDownDisabled} onClick={props.moveDown}
                    ><FontAwesomeIcon icon={faArrowDown}/></Button>
                </Tooltip>
                &#160;
            </>
            }
            <ListDelField name={props.name} icon={<DeleteOutlined />}/>
        </div>
    </div>
};
ListItem.defaultProps = {
    dense: true,
    allowOrdering:true
}
export default connectField(ListItem, {
    includeInChain: false,
    includeParent: true,
})