import {FastSchemeValidator} from "../FastSchemeValidator";
import ListFieldReorder from "../formFields/ListFieldReorder/ListFieldReorder";
import CheckboxField from "../formFields/CheckboxField";

/**
 *
 * More info at https://github.com/chomamateusz/list-field-reorder-demo/blob/master/src/schemas/array-of-arrays.js
 * More info at https://codesandbox.io/embed/bold-hill-2ol405jn6r
 *
 * @returns {JSONSchemaBridge}
 * @constructor
 */
const ObservationToolSchema = () => {
    return FastSchemeValidator({
        type: 'object',
        properties: {
            criteria: {
                "title": "Criterios",
                "type": "array",
                uniforms: {
                    component: ListFieldReorder
                },
                items: {
                    "title": " ",
                    "type": "object",
                    "id": "criteriaList",
                    "properties": {
                        "id": {
                            "type": "integer",
                            "options": {
                                "hidden": true
                            },
                            "title": "ID"
                        },
                        "name": {
                            "type": "string",
                            "title": "Criterio",
                        },
                        "code": {
                            "type": "string",
                            "title": "Código",
                        },/*
                        "description": {
                            "type": "string",
                            "title": "Descripción",
                        },*/
                        "persistence": {
                            "title": "Persistente",
                            "type": "boolean",
                            uniforms: {
                                component: CheckboxField
                            }
                        },
                        "informationNode": {
                            "title": "Fijo",
                            "type": "boolean",
                            uniforms: {
                                component: CheckboxField
                            }
                        },
                        "innerCategories": {
                            "title": "Categorías",
                            "type": "array",
                            uniforms: {
                                component: ListFieldReorder
                            },
                            "items": {
                                "type": "object",
                                "id": "categories",
                                "properties": {
                                    "id": {
                                        "options": {
                                            "hidden": true
                                        },
                                        "type": "integer",
                                        "title": "ID"
                                    },
                                    "name": {
                                        "type": "string",
                                        "title": "Categoría"
                                    },
                                    "code": {
                                        "type": "string",
                                        "title": "Código"
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    });
};
export default ObservationToolSchema;