import React, {useState} from 'react';
import {connectField} from 'uniforms';
import {Col, Row, Checkbox} from 'antd';

const noneIfNaN = x => (isNaN(x) ? false : x);

function CheckboxField(props) {
    const [currentVal, setCurrentVal] = useState(false);
    let onChange = (newValue) => {
        setCurrentVal(newValue);
        props.onChange(noneIfNaN(newValue));
    }
    return (
        <Row className={" ant-form-item"}>
            <Col span={12} className="ant-form-item-label">
                <label htmlFor="" className="" title="">
                    <span>{props.label}</span>
                </label>
            </Col>
            <Col span={12}>
                <Checkbox onChange={(v) => onChange(v)}
                          checked={typeof currentVal === 'boolean' ? currentVal : false}/>
            </Col>
        </Row>

    );
}

export default connectField(CheckboxField);

