import React from 'react';
import {connect} from 'react-redux';
import {fetchTool, saveTool} from '../../redux/lince/videoActions';
import PropTypes from "prop-types";
import ShowNotification from "../utility/ShowNotification";
import {AutoForm, ErrorsField, NestField, SubmitField} from "uniforms-antd";
import {Col, Divider, Row, Tooltip} from "antd";
import ListFieldReorder from "./data/formFields/ListFieldReorder/ListFieldReorder";
import ListItemField from "./data/formFields/ListFieldReorder/ListItemField";
import ObservationToolSchema from "./data/uniforms/ObservationToolSchema";
import {CheckSquareTwoTone, CloseSquareFilled, InfoCircleOutlined} from "@ant-design/icons";
import {LinceAutofield, LinceBoolfield} from "./data/formFields/StyledInputs";
import IntlMessages from "@iso/components/utility/intlMessages";


class LinceToolForm extends React.Component {
    componentWillMount() {
        this.props.fetchTool();
    }

    onSave(model) {
        const value = {data: model.criteria};
        this.props.saveTool(value);
        ShowNotification("Instrumento", "Tu modificación ha sido guardada! ")
    }

    render() {
        const _getSchema = () => {
            return ObservationToolSchema();
        }
        const model = {criteria: this.props.tool};
        return (
            <AutoForm schema={_getSchema()}
                      model={model}
                      placeholder={true}
                      onSubmit={model => this.onSave(model)}>
                <SubmitField value={<IntlMessages id="update"/>}/>
                <ErrorsField/>
                <Divider/>
                <ListFieldReorder name="criteria">
                    <ListItemField name="$" allowOrdering={true}>
                        <NestField showInlineError name={""}>
                            <Row>
                                <Col md={7}>
                                    {/*prefix={"yuhu"}size={"large"}
                                    \node_modules\antd\lib\input\Input.d.ts*/}
                                    <LinceAutofield name="name"/>
                                    <LinceAutofield name="code"/>
                                </Col>
                                <Col md={4}>
                                    <Row>
                                        <Col md={24} xs={24}>
                                            <Tooltip placement="leftTop" title="Un criterio persistente quedará marcado en la siguiente observación">
                                                <span>Persistente<InfoCircleOutlined style={{float:"left",marginRight:"5px"}}/></span>
                                                <LinceBoolfield name="persistence"
                                                                label={false}
                                                                checkedChildren={<CheckSquareTwoTone/>}
                                                                unCheckedChildren={<CloseSquareFilled/>}/>
                                            </Tooltip>
                                        </Col>
                                        <Col md={24} xs={24}>
                                            <Tooltip placement="bottomLeft"  title="Un criterio fijo no usa categorías y permite introducir texto">
                                                <span>Fijo<InfoCircleOutlined style={{float:"left",marginRight:"5px"}}/></span>
                                                <LinceBoolfield name="informationNode"
                                                                label={false}
                                                                checkedChildren={<CheckSquareTwoTone/>}
                                                                unCheckedChildren={<CloseSquareFilled/>}/>
                                            </Tooltip>
                                        </Col>
                                    </Row>
                                </Col>
                                <Col md={12} style={{marginLeft: "5px"}}>
                                    <Row>
                                        <Col md={12} xs={12}><b>Categoría</b></Col>
                                        <Col md={12} xs={12}><b>Código</b></Col>
                                    </Row>
                                    <ListFieldReorder name="innerCategories">
                                        <ListItemField name="$" allowOrdering={true}>
                                            <NestField name={""}>
                                                <Row style={{marginRight: "10px"}}>
                                                    <Col md={12} xs={12}>
                                                        <LinceAutofield name="name" label={false}/>
                                                    </Col>
                                                    <Col md={12} xs={12}>
                                                        <LinceAutofield name="code" label={false}/>
                                                    </Col>
                                                </Row>
                                            </NestField>
                                        </ListItemField>
                                    </ListFieldReorder>
                                </Col>
                            </Row>
                            <Row>
                                <Col md={24}>
                                    <Divider/>
                                </Col>
                            </Row>
                        </NestField>
                    </ListItemField>
                </ListFieldReorder>
                <Divider/>
                <SubmitField value={<IntlMessages id="update"/>}/>
            </AutoForm>
        );
    }
}

LinceToolForm.defaultProps = {
    tool: {data: [{}]}
};
LinceToolForm.propTypes = {
    tool: PropTypes.array.isRequired,
    fetchTool: PropTypes.func.isRequired
};
const mapStateToProps = store => ({
    tool: store.lince.tool
});
export default connect(mapStateToProps, {
    fetchTool: fetchTool,
    saveTool: saveTool
}, null, {forwardRef: true})(LinceToolForm);

